}

@media (max-width: 750px) {
  .description .top-nav-bar .menu .menu-btn {
    margin: 0;
    display: block; 
    outline: none;
    border: none;
    font-size: 25px;
    border: none;
    cursor: pointer;
    background-color: #ffffff;
    color: #000000;
  }

  .description .top-nav-bar .menu .menu-btn:focus {
    background-color: #ff0000;
  }
  .description .top-nav-bar .menu .menu-content {
    font-size: 15px;
    display: none;
    position: absolute;
    right: 5px;
    background-color: #f1f1f1;
    box-shadow: 0px 0px 4px 0 #eeeeee;
    z-index: 3;
  }
  
  .description .top-nav-bar .menu .menu-content a {
    float: none;
    color: #000000;
    padding: 10px 10px;
    text-decoration: none;
    display: block;
    text-align: left;
  }
}

@media (max-width: 750px) {
  .description 
  .top-nav-bar.top-nav-responsive 
  .menu 
  .menu-content {
    display: block;
  }
}

@media (max-width: 414px) {
  .description .top-nav-bar .menu .menu-btn {
